	import uniCountDown from '@/components/countDown.vue';
	export default {
		components: {
			uniCountDown
		},
		data() {
			return {
				color:'#b40000',
				checked:true,
				fontSize:'30rpx',
				showPayPass:false,
				showPassModal:false,
				payOther:false,
				orderVO:{},
				details:{},
				payVO:{},
				pwdVO:{
					focus: false,
					myArr: [0,1,2,3,4,5],        //输入框个数  
					isFocus: true,    //聚焦  
					Value: [],        //输入的内容  
					ispassword: false, //是否密文显示 true为密文， false为明文。
				},
				orderId:1,
				order:{},
				amount:100,
				hour:0,
				minute:0,
				second:0,
				show_time:0
			};
		},
		onLoad(options){
			this.orderVO = JSON.parse(options.order);
			var time = new Date(this.orderVO.endTime);
			console.log(time);
			this.hour = time.getHours();
			this.minute = time.getMinutes();
			this.second = time.getSeconds(); 
			this.show_time=1;
			console.log("hour---"+this.hour+"----"+this.minute+"----"+this.second);
		},
		onShow(){
	
		},
		methods:{
			createOrder(){
				let self = this
				let params=self.details
				self.$acFrame.HttpService.directBuy().then(res => {
					if(res.success){
						self.orderVO = res
					}
				})
			},
			switchChange(e,ind){
				let val = e.target.value
				let _data = this.orderVO.data.payMethodList[ind]
				this.payOther = val
				this.payVO.amount = _data.amount
				this.payVO.total  = _data.total
			},
			closeModal(){
				this.showPassModal = false
			},
			payResult(){
				let amount =this.payVO.amount
				let orderVO = this.orderVO
				//还需判断是否设置密码，如果没有设置支付密码，跳转设置支付密码
				if(amount>0&&amount<1000){
					this.showPassModal = true
					this.showPayPass = false
				} else if(amount>=1000){
					this.showPassModal = true
					this.showPayPass = true
				} else {
					this.showPassModal = false
					this.showPayPass = false
					//请求支付接口
					//支付结果:如果直接支付，直接跳转；如果拼单，增需请求拼单接口，带数据跳转
					uni.navigateTo({
						url:`payResult?res=${JSON.stringify(orderVO)}`
					})
				}
				
			},
			//参与拼单
            joinSpell(){
				let self = this
				let params={
					addressId:this.addrVO.id,
					goodsSkuId:this.details.chooseSpec.goodsSkuId,
					spellId:this.details.spellId
				}
				self.$acFrame.HttpService.joinSpell().then(res => {
					if(res.success){
						self.addrVO = res
					}
				})
			},
			//自主拼单
			createSpell(){
				let self = this
				let params={
					
				}
				self.$acFrame.HttpService.couponList().then(res => {
					if(res.success){
						self.addrVO = res
					}
				})
			},
			password_input: function (e) {
				let inputValue = e.detail.value;
				let self = this;
				let _arr=[]
				if(inputValue.length>0){
					for(let i=0;i<inputValue.length;i++){
						let val = inputValue.substr(i,1);
						_arr.push(val)
					}
				}
				self.pwdVO.Value = _arr
				if(_arr.length == this.pwdVO.Length){
					let params ={
						type:1,
                        passwd:inputValue
					}
					self.$acFrame.checkPaypwd.checkPaypwd(params).then(res => {
						if(res.success){
							//密码验证成功后  去支付
							//1.支付星票
							//2.支付钱
						}
					})
				}
			},
            payOtherWay(){
				let self = this
				let params ={
					bizId:0,
					passPay:false,
					type:1,
					passwd:inputValue
				}
				self.$acFrame.checkPaypwd.payByOther(params).then(res => {
					if(res.success){
						
					}

				})
					
			},
			Tap() {
				this.pwdVO.isFocus=true
			},

			getFocus: function () {
				this.pwdVO.focus=!this.pwdVO.focus
			},
		}
	}
